body {
  text-align: center;
  background: #f8f8f8;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

header {
  color: #fff;
  background: #4caf50;
  padding: 1rem;
}

main {
  background: #fff;
  width: 90%;
  max-width: 600px;
  margin: 20px auto;
  padding: 20px;
  box-shadow: 0 0 10px #0000001a;
}

input, textarea, select {
  width: 100%;
  margin: 10px 0;
  padding: 10px;
}

button {
  color: #fff;
  cursor: pointer;
  background: #4caf50;
  border: none;
  padding: 10px;
}

button:hover {
  background: #45a049;
}

ul {
  padding: 0;
  list-style: none;
}

li {
  background: #ddd;
  justify-content: space-between;
  margin: 10px 0;
  padding: 10px;
  display: flex;
}

#ai-chatbot {
  color: #fff;
  text-align: center;
  background-color: #222;
  border-radius: 10px;
  width: 90%;
  max-width: 500px;
  margin: 20px auto;
  padding: 15px;
  box-shadow: 0 4px 10px #00000080;
}

#chatbox {
  flex-direction: column;
  gap: 10px;
  display: flex;
}

#chat-messages {
  text-align: left;
  background-color: #333;
  border-radius: 5px;
  flex-direction: column;
  gap: 8px;
  height: 300px;
  padding: 10px;
  font-size: 16px;
  display: flex;
  overflow-y: auto;
}

.user-message {
  color: #fff;
  text-align: left;
  white-space: pre-wrap;
  background-color: #007bff;
  border-radius: 15px;
  align-self: flex-end;
  max-width: 80%;
  padding: 10px;
  font-weight: bold;
}

.ai-message {
  color: #fff;
  text-align: left;
  white-space: pre-wrap;
  background-color: #28a745;
  border-radius: 15px;
  align-self: flex-start;
  max-width: 80%;
  padding: 10px;
}

#chat-input {
  color: #fff;
  background-color: #444;
  border: 1px solid #ddd;
  border-radius: 8px;
  width: calc(100% - 20px);
  padding: 12px;
  font-size: 16px;
}

#chat-send {
  color: #fff;
  cursor: pointer;
  background-color: #28a745;
  border: none;
  border-radius: 8px;
  padding: 12px 20px;
  font-size: 16px;
  transition: background .3s;
}

#chat-send:hover {
  background-color: #218838;
}
